___CSS_LOADER_EXPORT___.push([module.id, `nav {
    display: flex;
    padding-top: 8px;
    justify-content: space-between;
    align-items: center;
}

.logo-box {
    display: flex;
    align-items: center;
    gap: 4px;
    color: var(--text-primary);
}

.auth-box {
    display: flex;
    align-items: center;
    gap: 16px;
}

/* .profile-container {
} */

.profile-btn {
    display: flex;
    width: 32px;
    height: 32px;
    align-items: flex-start;
    flex-shrink: 0;
    border-radius: 16px;
}

.dropdown-container {
    position: absolute;
    /* display: inline-block; */
}

.dropdown-content {
    display: flex;
    /* width: 201px; */
    padding: 16px;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    border-radius: 12px;
    border: 1px solid var(--background-tertiary, rgba(41, 44, 47, 0.12));
    background: var(--background-primary, #fffaff);
}
